.site-list {
  padding-left: 0;
  margin-bottom: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 22px;
  width: 589px;
  max-width: 680px;

  user-select: none;
  list-style-type: none;


  @include vp-1199 {
    width: auto;
    max-width: 500px;
    gap: 11px;

    &--dropdown-menu {
      flex-direction: column;
      align-items: flex-start;
      gap: 12px;
      padding-top: 43px;
      padding-right: 40px;
      padding-bottom: 64px;
      padding-left: 40px;
      width: 100%;
      max-width: 100%;
      position: absolute;
      top: 74px;
      left: 0;

      background-color: $color-default-white;
    }
  }


  @include vp-767 {
    gap: 11px;
    padding-top: 44px;
    padding-left: 10px;

    &--dropdown-menu {
      padding-bottom: 43px;
      top: 47px;
      gap: 6px;
    }
  }

  &--footer {
    width: auto;
    max-width: 350px;

    @include vp-767 {
      flex-direction: column;
      align-items: flex-start;
      padding-left: 0;
      padding-top: 0;
      row-gap: 0;
    }
  }
}

.site-list__link {
  padding: 3px 5px 2px;
  position: relative;
  z-index: 3;
  display: inline-flex;
  max-width: 195px;
  border-bottom: 1px solid $color-transparent;

  color: inherit;

  @include hover {
    &:not(.site-list__link--disabled) {
      border-bottom: 1px solid;
    }
  }

  @include focus {
    &:not(.site-list__link--disabled) {
      outline: 1px solid;
      border-bottom: 0;
    }
  }

  @include active {
    &:not(.site-list__link--disabled) {
      outline: none;
      border-bottom: 0;
    }
  }

  &--disabled {
    cursor: default;
    opacity: 0.5;
    outline: none;
  }

  @include vp-1199 {
    font-size: 14px;

    &--dropdown-menu {
      font-size: 16px;
      font-weight: 700;
      text-transform: uppercase;
      letter-spacing: 0.6px;
    }
  }

  @include vp-767 {
    font-size: 14px;
  }
}
